<template>
  <div id="app">
    <router-view />
    <tabbar></tabbar>
  </div>
</template>

<script>
import Tabbar from './components/Tabbar/MainTabbar'
export default {
  name: 'App',
  components: {
    Tabbar
  },
  mounted () {
    /* eslint-disable */
    if (window.plus) {
      plus.key.addEventListener('backbutton', function () {
        // 事件处理 
        plus.nativeUI.confirm("退出程序？", function (event) {
          if (event.index) {
            plus.runtime.quit();
          }
        }, null, ["取消", "确定"]);
      }, false);
    }
  }
}
</script>

<style scoped>
</style>



