<template>
  <div>
    <login-head>
      <template #left>
        <i class="el-icon-back"></i>
      </template>
      <template #content>
        登录
      </template>
    </login-head>
    <div class="wrapper">
      <div class="container">
        <div class="head-img">
          <img src="../../../static/images/head.jpg">
        </div>
        <el-form
          label-position="right"
          :model="userdata"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item prop="username">
            <el-input
              v-model="userdata.username"
              placeholder="账号"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              v-model="userdata.password"
              :type="showType"
              placeholder="密码"
              autocomplete="off"
            ><i
                slot="suffix"
                class="element-icons"
                :class="eyes"
                @click="lookOrHide"
              ></i></el-input>
          </el-form-item>
        </el-form>
        <el-button
          type="success"
          plain
          @click="login"
        >登录/注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import LoginHead from '@/components/PageHeader/PageHeader'
import { loginTo } from '@/request/api'
export default {
  name: 'Login',
  data () {
    return {
      userdata: {
        username: '',
        password: '',
        userimg: ''
      },
      eyes: 'el-iconclose-eye',
      rules: {
        username: [
          { required: true, message: '请输入帐号', trigger: 'blur' },
          { min: 3, max: 11, message: '账号长度在3-11个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 3, max: 11, message: '密码长度在3-11个字符', trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    LoginHead
  },
  methods: {
    lookOrHide () {
      this.eyes = this.eyes === 'el-iconclose-eye' ? 'el-iconyanjing' : 'el-iconclose-eye'
    },
    login () {
      if (this.userdata.username.trim() === '' || this.userdata.password.trim() === '') {
        return
      }
      loginTo(this.userdata)
        .then(res => {
          console.log(res)
          if (res.data.result.status === 0) {
            alert('密码错误或帐号已被注册')
          } else {
            localStorage.username = this.userdata.username
            localStorage.token = res.data.token
            this.$router.replace({ name: 'Person' })
          }
          localStorage.imageUrl = res.data.result.userimg
        })
        .catch(() => {
          alert('网络连接失败')
        })
    }
  },
  computed: {
    showType () {
      return this.eyes === 'el-iconclose-eye' ? 'password' : 'text'
    }
  }
}
</script>

<style scoped>
.head-img {
  width: 6rem;
  height: 6rem;
}

img {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.el-form-item {
  margin: 1.2rem 0.1rem;
}

.el-button {
  width: 10rem;
  margin-top: 0.5rem;
}
</style>



